
import { _decorator, Component, Node, Collider2D, Contact2DType, IPhysics2DContact, Prefab, instantiate, CanvasComponent, director } from 'cc';
import { IconSpawn } from './IconSpawn';
const { ccclass, property } = _decorator;
 
@ccclass('BulletIcon')
export class BulletIcon extends Component {
    // @property({type: Player})
    // public player: Player|null = null;

    // @property({type: BulletAct})
    // public bullet: BulletAct|null = null;

    start () {
        //collider
        let collider = this.getComponent(Collider2D);
        if (collider) {
            collider.on(Contact2DType.BEGIN_CONTACT, this.onBeginContact, this);
        }
    }

    onBeginContact(selfCollider: Collider2D, otherCollider: Collider2D, contact: IPhysics2DContact | null) 
    {       
        if (otherCollider.node.name == "Player")
        {
            //spawn bullet
            //this.bullet.getComponent(BulletAct).SetActiveBullet();

            this.node.getComponent(IconSpawn).Respawn();
        }
        else
        {
            this.node.getComponent(IconSpawn).Respawn();
        }
    }
}
